<template>
  <div class="home">
      <div class="search">
        <input type="text" placeholder="请输入要查询的手机号/QQ号/微信号/身份证号/姓名" v-model="content">
        <p @click="blacksSearch">查询</p>
      </div>
      <div v-if="!show" style="height: 600px;"></div>
      <div class="null" v-if="!showBlack && show">
        <img src="../../images/null.png" alt="">
        <span>未查询到信息～</span>
        <p @click="$router.push('/disclose')">我要举报</p>
      </div>
      <div class="details" v-if="showBlack && show">
        <div class="item" style="margin-top: 64px;">
          <h3>微信号：</h3>
          <p>{{black.qq_no}}</p>
        </div>
        <div class="item">
          <h3>QQ号：</h3>
          <p>{{black.wx_no}}</p>
        </div>
        <div class="item">
          <h3>手机号：</h3>
          <p>{{black.mobile}}</p>
        </div>
        <div class="item">
          <h3>姓名：</h3>
          <p>{{black.name}}</p>
        </div>
        <div class="item">
          <h3>证件号：</h3>
          <p>{{black.id_no}}</p>
        </div>
        <div class="item">
          <h3>被骗币种：</h3>
          <p>{{black.symbol_id}}</p>
        </div>
        <div class="item">
          <h3>骗子钱包地址：</h3>
          <p>{{black.wallet_address}}</p>
        </div>
        <div class="item" style="align-items: flex-start">
          <h3>举报内容：</h3>
          <p>{{black.black_content}}</p>
        </div>
        <div class="item" style="align-items: flex-start">
          <h3>证件信息：</h3>
          <div>
            <img :src="item" alt="" v-for="(item,index) in black.id_card_images" style="width: 260px;height: 160px;">
          </div>
        </div>
        <div class="item" style="align-items: flex-start">
          <h3>举报截图：</h3>
          <div>
            <img :src="item" alt="" v-for="(item,index) in black.images" style="width: 140px;height: 248px;">
          </div>
        </div>
      </div>
    </div>
</template>

<script>
  import { httpGet } from '../../utils/index'
  export default {
    data () {
      return {
        input: '',
        content: '',
        black: [],
        all_blacks: [],
        activeIndex: 0,
        dir: 1,
        showBlack: false,
        show: false
      }
    },
    mounted () {
      this.getAllBlacks()
    },
    methods: {
      blacksSearch () {
        const that = this
        if (!that.content) {
          this.$message.error('请输入搜索内容')
          return
        }
        httpGet('/blacks/search', {content: that.content}, res => {
          that.show = true
          if (res.data.error_code === 0) {
            that.black = res.data.black
            that.showBlack = true
            that.content = ''
          } else {
            that.showBlack = false
          }
        })
      },
      getAllBlacks () {
        const that = this
        httpGet('/blacks/all_blacks', {}, res => {
          if (res.data.error_code === 0) {
            that.all_blacks = res.data.blacks
          }
        })
      }
    }
  }
</script>

<style scoped>
  .home{
    width: 1920px;
    background-color: #000;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 50px;
  }
  .search{
    width: 828px;
    height: 80px;
    background: #FFFFFF;
    border: 1px solid #979797;
    border-radius: 8px;
    display: flex;
    overflow: hidden;
    margin-top: 95px;
  }
  .search input{
    width: 648px;
    height: 80px;
    font-size: 20px;
    color: #999999;
    padding-left: 20px;
  }
  .search p{
    width: 160px;
    height: 80px;
    background: #00488B;
    line-height: 80px;
    text-align: center;
    font-size: 24px;
    color: #FFFFFF;
  }
  .null{
    width: 1200px;
    background-color: #fff;
    margin-top: 50px;
    padding-bottom: 65px;
    height: 530px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .null img{
    width:162px;
    height: 130px;
  }
  .null span{
    font-size: 16px;
    color: #999999;
    margin-top: 20px;
  }
  .null p{
    width: 233px;
    height: 50px;
    background: #00488B;
    border-radius: 4px;
    line-height: 50px;
    text-align: center;
    font-size: 16px;
    color: #FFFFFF;
    margin-top: 40px;
  }
  .details{
    width: 1200px;
    background-color: #fff;
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-bottom: 65px;
  }
  .details .item{
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 110px;
    padding-right: 38px;
    margin-top: 24px;
  }
  .details .item h3{
    width: 112px;
    margin-right: 33px;
    text-align: left;
    font-size: 16px;
    color: #666666;
  }
  .details .item p{
    font-size: 16px;
    color: #333333;
  }
  .details .item div{
    width: 826px;
    height: auto;
    overflow: hidden;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap:wrap;
  }
  .details .item div img{
    margin-right: 10px;
  }
</style>



